.add-item-form-container {
  align-items: center;
  margin-bottom: 10px;
}

.add-item-input {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.add-item-button {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.add-item-button:hover {
  background-color: #0056b3;
}

.toggle-button {
  background-color: #000000;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: large;
  margin-top: 10px;
}

.toggle-button:hover {
  background-color: #007BFF;
}
.main-button {
  background-color: #f0f0f0;
  color: #000000;
  margin-left: 20px;
  border: 1px solid;
  border-radius: 4px;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: larger;
  margin-top: 20px;
}

.main-button.archived {
  background-color: #000000;
  color: #ffffff;
}

.main-button:hover {
  background-color: #000000;
  color: #ffffff;
}

.delete-button {
  background-color: #f0f0f0;
  color: #000000;
  margin-left: 20px;
  border: 1px solid;
  border-radius: 4px;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: larger;
  margin-top: 20px;
}

.delete-button:hover {
  background-color: red;
  color: #ffffff;
}

.button-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.remove-button {
  background-color: #007BFF;
}

.remove-button:hover {
  background-color: red;
}

/* Dark Mode Styles */

.dark-mode .main-button {
  background-color: #000000;
  color: #ffffff;
}

.dark-mode .main-button.archived {
  background-color: #ffffff;
  color: #000000;
}

.dark-mode .main-button:hover {
  background-color: #ffffff;
  color: #000000;
}

.dark-mode .delete-button {
  background-color: #000000;
  color: #ffffff;
}

.dark-mode .delete-button:hover {
  background-color: red;
  color: white;
  border: black;
}